import React from "react"
import Layout from "./Layout";
import {graphql} from 'gatsby'
import {SEO} from "theme-seo";
import Img from "gatsby-image";

const Videos = ({data, pageContext}) => {
    const markdownNodes = data.allMarkdownRemark.nodes
        .sort((a,b) => new Date(a.parent.birthTime) - new Date(b.parent.birthTime))
        .sort((a, b) => b.frontmatter.order - a.frontmatter.order );

    return (
        <Layout>
            <SEO title={pageContext.title}/>
            {data.allFile.nodes.length !== 0 && <Img alt="Videos" fluid={data.allFile.nodes[0].childImageSharp.fluid}/>}
            <div
                className="flex flex-wrap my-24 justify-center md:flex-col lg:flex-row lg:justify-start w-10/12 lg:w-5/6 mx-auto shadow-xl">
                {markdownNodes ? (markdownNodes.map(node => (
                    <div key={node.frontmatter.fileName} className="md:w-11/12 lg:w-1/3 my-3 ">
                        <div className="p-2 m-4">
                            <div className="mx-2 p-2">
                                <video className="w-full h-64 mx-auto" src={pageContext.videoUrl + node.frontmatter.fileName}
                                       controls
                                       preload="none"
                                />
                            </div>
                            <div className="text-gray-700 text-2xl m-4">{node.frontmatter.title}</div>
                            <div className="text-gray-600 mx-4"
                                 dangerouslySetInnerHTML={{__html: node.html}}></div>
                        </div>

                    </div>
                ))) : (
                    <div className="m-auto text-purple-600 my-10 font-bold text-4xl">There is no videos to show</div>)}
            </div>
        </Layout>
    )
}

export default Videos;

export const query = graphql`
query allVideos {
  allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/video/"  }})
   {
    nodes {
      html
      fields {
        slug
      }
      frontmatter {
        fileName
        description
        title
        order
      }
      parent {
          ... on File {
            birthTime(formatString: "MM/DD/YYYY")
          }
        }
    }
  }
  allFile(filter: { sourceInstanceName: { eq: "video" }, extension:{eq: "jpg"}, name:{eq:"video"}}) {
    nodes {
      name
      childImageSharp {
        fluid (maxWidth: 2500 ) {
          ...GatsbyImageSharpFluid
        }
       }
    }
  }
}`;